






























































import { Component, Vue } from 'vue-property-decorator'
import axios from 'axios'
import { isAxiosError, ruleMatchString, ruleMinLength } from '@/utils'

@Component({
  components: {},
})
export default class RegisterPage extends Vue {
  username = ''
  password = ''
  confirmPassword = ''
  loading = false
  validForm = false
  $refs!: {
    username: HTMLInputElement
    form: HTMLFormElement
  }
  showSnackbar = false
  snackbarMessage = ''

  ruleValidUsername = (value: string): true | string =>
    value.match(/^([a-zA-Z0-9._-])*$/) !== null ||
    this.$tc('register.invalid-chars')

  ruleMatchString = ruleMatchString
  ruleMinLength = ruleMinLength

  mounted(): void {
    this.$refs.username.focus()
  }

  async register(): Promise<void> {
    if (!this.$refs.form.validate()) return

    this.loading = true

    try {
      const response = await axios.post<{ token: string }>('/auth/register', {
        username: this.username,
        password: this.password,
      })

      this.username = ''
      this.password = ''
      this.confirmPassword = ''
      this.$refs.form.resetValidation()

      this.$store.commit('setSessionToken', response.data.token)
      this.$router.push('/')

      this.$store.dispatch('getUser')
      this.$store.dispatch('getPortfolios')
      this.$store.dispatch('getCurrencies')
    } catch (err) {
      if (
        isAxiosError<{ message: string }>(err) &&
        err.response?.status === 400
      ) {
        this.snackbarMessage = err.response.data.message
      } else {
        this.snackbarMessage = String(err)
      }
      this.showSnackbar = true
    } finally {
      this.loading = false
    }
  }
}
